@import url(https://fonts.googleapis.com/css2?family=Barlow&display=swap);
.article{margin:0 auto;padding:25px 30px;min-width:300px;max-width:500px;border-radius:5px;box-shadow:rgba(149,157,165,0.2) 0 8px 24px;background:linear-gradient(to left, #457fca, #5691c8);transition:-webkit-transform 0.3s;transition:transform 0.3s;transition:transform 0.3s, -webkit-transform 0.3s;color:white}.article:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.article__date{display:block;text-align:end}

body{font-family:"Barlow", sans-serif;display:flex;justify-content:center;align-items:center;height:100vh;background:linear-gradient(to left, #4b79a1, #283e51);background-size:cover;background-repeat:no-repeat}

